import { NativeModules } from 'react-native';
import isString from 'lodash/isString';
import isArray from 'lodash/isArray';

/**
 * 判断是否是低端机
 */
export function isLowPhone(): boolean {
    let isLowPhoneTag = false;
    const useAgent = NativeModules.Kds.userAgent;
    if (isString(useAgent)) {
        const arr = useAgent.split(' ');
        if (isArray(arr)) {
            for (let i = 0; i < arr.length; i++) {
                if (
                    isString(arr[i]) &&
                    arr[i].includes('ISLP') &&
                    arr[i].endsWith('1')
                ) {
                    isLowPhoneTag = true;
                }
            }
        }
    }
    return isLowPhoneTag;
}
